
.black{
  background-color:rgba(0,0,0,0.6) !important;
  height: 130px !important;
  transition: 0.3s !important;
  color: #fff;

}
.nav__section {
  width: 100%;
  position: fixed; 
  top: 0;
  left: 0;
  z-index: 100;
  transition: 0.3s;
}

.logo-img{
  width: 130px;
}
.logo-img img{
  width: 100%;
}

.nav__section:hover {
  background-color:rgba(0,0,0,0.6);
}

.nav__section:hover .nav_list__item a {
  transition: 0.3s;
}

/* #141d31; */

.nav__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

ul {
  margin: 0;
  padding: 0;
}

.nav_list__item {
  cursor: pointer;
}

.nav__list {
  width: 40%;
  color: #fff;
  display: flex;
  list-style: none;
  justify-content: space-around;
}
