 .slider{
  overflow: hidden;
}

.slider figure{
  position: relative;
  margin: 0;
  width: 500%;
  left: 0;
  -webkit-animation: slider 20s ease-in-out infinite;
          animation: slider 20s ease-in-out infinite;
}

 .slider figure img{
  width: 20%;
  float: left;
  
}
.section__sec1 {
  width: 10%;
  height: 100vh;
  object-fit: cover;
}

@-webkit-keyframes slider{
  0%{left: 0;}
  20%{left: 0;}
  25%{left: -100%;}
  45%{left: -100%;}
  50%{left: -200%;}
  70%{left: -200%;}
  75%{left: -300%;}
  95%{left: -300%;}
  100%{left: -400%;}
}

@keyframes slider{
  0%{left: 0;}
  20%{left: 0;}
  25%{left: -100%;}
  45%{left: -100%;}
  50%{left: -200%;}
  70%{left: -200%;}
  75%{left: -300%;}
  95%{left: -300%;}
  100%{left: -400%;}
}




.section__intro {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  width: 70%;
 
}

.section__intro h3 {
  color: #fff;
  font-size: 55px;
  text-align: center;
  font-style: italic;
  font-weight: normal;
  font-family: 'Arvo', serif;
  margin: 0
}

.section__intro h1 {
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 79px;
  margin: 0;
}


.section__intro a {
  position: absolute;
  top: 150%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 4px solid white;
  width: 250px;
  height: 70px;
  text-align: center;
  line-height: 65px;
  border-radius: 60px;
  text-transform: uppercase;
  text-decoration: none;
  overflow: hidden;
  }
.section__intro  a span {
  font-size: 25px;
  color: white;
  mix-blend-mode: difference;
  }
  
 .section__intro a:hover:before {
  content: '';
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 10%;
  background-color: #00fff5;
  -webkit-transform: skew(-15deg);
          transform: skew(-15deg);
  -webkit-animation: animation 1s ease-in-out;
          animation: animation 1s ease-in-out;
  box-shadow: 0 2px 10px #00fff5;
  }
  
  
  @-webkit-keyframes animation {
  0% {
  opacity: 0;
  left:-50px;
  opacity: 1;
  }
  90% {
  left: 100%;
  opacity: 0.5; ;
  }
  100% {
  opacity: 0;
  }
  }
  
  
  @keyframes animation {
  0% {
  opacity: 0;
  left:-50px;
  opacity: 1;
  }
  90% {
  left: 100%;
  opacity: 0.5; ;
  }
  100% {
  opacity: 0;
  }
  }
  .section__intro a:hover{
  color: #00fff5;
  border: 4px solid #00fff5;
  box-shadow: 0 0 5px #00fff5;
  }


