.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 1rem 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.col {
  -webkit-flex: 32% 1;
          flex: 32% 1;
  max-width: 32%;
  position: relative;
  overflow: hidden;
  border-radius: 15px;
}


.col img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  transition: 0.5s;
  -webkit-filter: blur(1px);
          filter: blur(1px);
}

.col .content {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -40%);
          transform: translate(-50%, -40%);
}


.col .content {
  color: white;
  position: absolute;
  top:50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -10%);
          transform: translate(-50%, -10%);
  transition: 0.8s;
}

.col:hover .content {
  opacity: 1;
  -webkit-transform: translate(-50%, -50%) scale(1.2);
          transform: translate(-50%, -50%) scale(1.2); 
   
 
}

.col:hover img {
  -webkit-filter: blur(0px);
          filter: blur(0px);
  -webkit-transform: scale(1.1);
          transform: scale(1.1); 
  box-shadow: 0 12px 16px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.col:after {
  content: '';
  width:100%;
  height: 50%;
  background: rgb(233,25,25); 
  background: linear-gradient(0deg, #ff2525 50%, transparent);
  position: absolute;
  bottom: 0%;
  display: inherit;
  overflow: hidden;
  opacity: 0.6;
  -webkit-transform: scale(1.3);
          transform: scale(1.3)
}



